<template>
  <Page
    v-loading="loading"
    bodyClass="p-lr-60"
    x="c"
    y="c"
    :title="$t(`help.${name}`)"
  >
    <Column v-if="!loading" :key="name" class="break-word" v-html="helpHTML" />
  </Page>
</template>

<script setup>
import * as constant from '@/utils/constant.js'

const props = defineProps({
  name: {},
})
const languageStore = useLanguageStore()
let loading = $ref(true)
let helpHTML = $ref('')

const getHelpHTML = async () => {
  const langMessagesMap = {
    [constant.LANGCN]: () => import('@/language/zh/helpHTML.js'),
    [constant.LANGEN]: () => import('@/language/en/helpHTML.js'),
    [constant.LANGTW]: () => import('@/language/tw/helpHTML.js'),
  }
  const htmls = (await langMessagesMap[languageStore.locale]()).default.helpHTML
  loading = false
  helpHTML = htmls[props.name]
}

const onClickLeft = () => {
  router.go(-1)
}

onBeforeMount(() => {
  getHelpHTML()
})

</script>

